/*================================================
Default CSS
=================================================*/
body {
  font-family: "Poppins", sans-serif;
  padding: 0;
  margin: 0;
  font-size: 15px;
}

p {
  font-size: 15px;
  color: #848484;
  line-height: 1.9;
  margin-bottom: 15px;
  margin-top: 0;
}

p:last-child {
  margin-bottom: 0;
}

img {
  max-width: 100%;
}

.uk-h1,
.uk-h2,
.uk-h3,
.uk-h4,
.uk-h5,
.uk-h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  font-family: "Poppins", sans-serif;
  margin-top: 15px;
  margin-bottom: 15px;
  color: #000000;
}

a {
  outline: 0 !important;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  display: block;
}

a:hover, a:focus {
  text-decoration: none;
  outline: 0;
}

.ptb-100 {
  padding-top: 100px;
  padding-bottom: 100px;
}

.d-table {
  width: 100%;
  display: table;
  height: 100%;
}

.d-table-cell {
  vertical-align: middle;
  display: table-cell;
}

.bg-gray {
  background-color: #f4f4f4;
}

.uk-dark {
  background-color: #000000;
}

/*uk-default-btn*/
.uk-button-default {
  border: 2px solid #ff4800;
  background-color: #ff4800;
  color: #ffffff;
}

.uk-button-default:hover, .uk-button-default:focus {
  background: #ffffff;
  color: #ffffff;
  border-color: #ff4800;
}

.uk-button-default i {
  margin-left: 3px;
}

.uk-button {
  font-size: 15px;
  font-weight: 600;
  line-height: unset;
  text-transform: capitalize;
  padding: 13px 32px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  position: relative;
  z-index: 1;
}

.uk-button::before, .uk-button::after {
  content: "";
  position: absolute;
  height: 0;
  width: 51%;
  background: #ff4800;
  z-index: -1;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.uk-button::after {
  left: 0;
  top: 0;
}

.uk-button::before {
  right: 0;
  bottom: 0;
}

.uk-button:hover::before, .uk-button:hover::after, .uk-button:focus::before, .uk-button:focus::after {
  height: 100%;
}

/*section-title*/
.section-title {
  margin-bottom: 60px !important;
  position: relative;
}

.section-title .bar {
  position: relative;
  z-index: 1;
  background: #ff4800;
  width: 55px;
  height: 3px;
  margin-top: 12px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.section-title .bar::before, .section-title .bar::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 3px;
  top: 0;
  background: #ffffff;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.section-title .bar::after {
  right: 10px;
}

.section-title .bar::before {
  right: 20px;
}

.section-title span {
  margin-bottom: -5px;
  color: #ff4800;
  display: block;
  text-transform: uppercase;
}

.section-title h2 {
  font-size: 34px;
  font-weight: 700;
}

.section-title .uk-button {
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -25px;
}

.section-title.uk-text-center .bar {
  margin-left: auto;
  margin-right: auto;
}

/*uk-input*/
.uk-input,
.uk-select:not([multiple]):not([size]) {
  height: 47px;
}

.uk-input,
.uk-select,
.uk-textarea {
  padding: 10px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.uk-input:focus,
.uk-select:focus,
.uk-textarea:focus {
  border-color: #ff4800;
  padding-left: 15px;
}

/*================================================
Preloader CSS
=================================================*/
.uk-preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999999;
  background: #ff4800;
  left: 0;
  right: 0;
}

.uk-preloader .spinner {
  width: 70px;
  height: 70px;
  position: absolute;
  margin: 0 auto;
  right: 0;
  left: 0;
  top: 50%;
  margin-top: -70px;
}

.uk-preloader .spinner .double-bounce1,
.uk-preloader .spinner .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-bounce 2s infinite ease-in-out;
          animation: sk-bounce 2s infinite ease-in-out;
}

.uk-preloader .spinner .double-bounce2 {
  opacity: 0.8;
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}

@-webkit-keyframes sk-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes sk-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

